  <v-card class="pa-0"
          :elevation="0">
    <v-card-text>
      <v-row dense>
        <v-col class="d-flex"
               cols="12"
               sm="3">
          <v-switch :true-value="1"
                    :false-value="0"
                    v-model="params.status"
                    :label="params.status?$t('label_To_enable_the'): $t('label_disable')"></v-switch>
        </v-col>
      </v-row>
      <v-row>
        <!-- <v-col cols="12"
               class="d-flex"
               sm="6">
          <mrc_ess_input :label="$t('label_account')+'*'"
