                         @input="$v.params.user_account.$touch()"
                         @blur="$v.params.user_account.$touch()"
                         :disabled='Boolean(params.uuid)'
                         v-model.trim="params.user_account"
                         placeholder='请输入账号'></mrc_ess_input>
        </v-col>
        <v-col cols="12"
               class="d-flex flex-column "
               sm="6">

          <mrc_ess_input :label="$t('label_password')+'*'"
                         type='password'
                         @input="$v.params.user_pwd.$touch()"
                         @blur="$v.params.user_pwd.$touch()"
                         v-model.trim="params.user_pwd"
                         placeholder='请输入密码'
                         :disabled='Boolean(params.uuid)'
                         :error-messages="$errors('params','user_pwd',requiredRule)"></mrc_ess_input>
          <span v-if="params.uuid"
                @click="resetPassword"
                style="color: #ff8a00;position:relative;top: 10px;cursor: pointer">重置密码</span>
        </v-col>
        <v-col cols="12"
               class="d-flex"
               sm="6">
          <mrc_ess_input :label="$t('label_The_name')+'*'"
                         :error-messages="$errors('params','user_name',requiredRule.concat(maxLength(20)))"
                         @input="$v.params.user_name.$touch()"
                         @blur="$v.params.user_name.$touch()"
                         v-model.trim="params.user_name"
                         :readonly='disabled'
                         placeholder='请输入名字'></mrc_ess_input>
        </v-col>
        <v-col cols="12"
               class="d-flex"
               sm="6">

          <mrc_ess_input label='电子邮箱*'
                         :error-messages="$errors('params','user_primary_email',requiredRule.concat(emailRule))"
                         @input="$v.params.user_primary_email.$touch()"
                         @blur="$v.params.user_primary_email.$touch()"
                         v-model.trim="params.user_primary_email"
                         :readonly='disabled'
                         placeholder='请输入电子邮箱'></mrc_ess_input>
        </v-col> -->
        <v-col cols="12"
               class="d-flex"
               sm="6">
          <mrc_ess_multiText :label="$t('label_account')+'*'"
                             style='width:100%'
                             :error-messages="$errors('params','user_account',requiredRule.concat(maxLength(20)))"
                             :disabled='Boolean(params.uuid)'
                             v-model.trim="params.user_account"
                             :isNational="judegNational('user_account')"
                             :placeholder="$t('label_account')"></mrc_ess_multiText>
        </v-col>
        <v-col cols="12"
               class="d-flex flex-column "
               sm="6">

          <mrc_ess_multiText :label="$t('label_password')+'*'"
                             style='width:100%'
                             type='password'
                             v-model.trim="params.user_pwd"
                             :isNational="judegNational('user_pwd')"
                             :placeholder="$t('label_password')"
                             :disabled='Boolean(params.uuid)'
                             :error-messages="$errors('params','user_pwd',requiredRule)"></mrc_ess_multiText>
          <span v-if="params.uuid"
                @click="resetPassword"
                :style="{color: $vuetify.theme.defaults.light.primary,position:'relative',top: '10px',cursor: 'pointer'}">{{$t('label_To_reset_your_password')}}</span>
        </v-col>
        <v-col cols="12"
               class="d-flex"
               sm="6">
          <mrc_ess_multiText :label="$t('label_The_name')+'*'"
                             style='width:100%'
                             :error-messages="$errors('params','user_name',requiredRule.concat(maxLength(20)))"
                             v-model.trim="params.user_name"
                             :isNational="judegNational('user_name')"
                             :readonly='disabled'
                             :placeholder="$t('label_The_name')"></mrc_ess_multiText>
        </v-col>
        <v-col cols="12"
               class="d-flex"
               sm="6">

          <mrc_ess_multiText :label='$t("label_Personal_mailbox")+"*"'
                             style='width:100%'
                             :error-messages="$errors('params','user_primary_email',requiredRule.concat(emailRule))"
                             v-model.trim="params.user_primary_email"
                             :isNational="judegNational('user_primary_email')"
                             :readonly='disabled'
                             :placeholder='$t("label_Personal_mailbox")'></mrc_ess_multiText>
        </v-col>
        <v-col cols="12"
               class="d-flex"
               sm="12">
          <mrc_ess_prompt obj='mrbase_sys_clients'
                          :label="$t('label_Responsible_for_the_customer')"
                          multiple
                          :disabled='disabled'
                          v-model="computed_clients_uuid"></mrc_ess_prompt>
          <!-- :errMsg="$errors('params','clients_uuid',requiredRule)" -->
        </v-col>

      </v-row>
    </v-card-text>
    <mrc_ess_submitBtn @cancel='cancel'
                       v-if="!disabled"
                       :loading='loading'
                       @success='success'
                       :cancelText="cancelText || $t('label_cancel')"
                       :successText="successText || $t('label_submit')"></mrc_ess_submitBtn>
  </v-card>
</template>

<script>
import { required, email, maxLength } from 'vuelidate/lib/validators'
import { mrc_admin_reset } from '@/service/api/mrc_system_admin'

export default {
  components: {},
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    cancelText: {
      type: String
    },
    successText: {
      type: String
    },
    paramsData: {
      type: Object
    },
    loading: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      params: {
        status: 1,
        user_account: '',
        user_pwd: '',
        user_primary_email: '',
        clients_uuid: '',
        user_name: ''
      }
    };
  },
  validations: {
    params: {
      user_account: {
        required,
        maxLength: maxLength(20)
      },
      user_pwd: {
        required
      },
      user_name: {
        required,
        maxLength: maxLength(20)
      },
      user_primary_email: {
        required,
        email
      },
      // clients_uuid: {
      //   required
      // }
    }
  },
  computed: {
    computed_clients_uuid: {
      get () {
        if (this.params.clients_uuid) {
          return this.params.clients_uuid.indexOf(',') > -1
            ?
            this.params.clients_uuid.split(',')
            :
            [this.params.clients_uuid]
        } else {
          return []
        }
      },
      set (val) {
        this.params.clients_uuid = val.join()
      }
    }
  },
  created () { },
  watch: {
    paramsData: {
      handler (val) {
        this.params = val
      },
      deep: true
    }
  },
  mounted () {
    if (this.paramsData) {
      this.params = this.paramsData
    }
  },

  methods: {
    success () {
      this.$v.params.$touch()
      if (this.$v.params.$error) return
      this.$emit('success', this.params)
    },
    cancel () {
      this.$emit('cancel')
    },
    async resetPassword () {
      const reset = await mrc_admin_reset({ uuid: this.params.uuid })
      if (reset.code == 200) {
        this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
      }
    }
  },
};
</script>

